import Component from 'flarum/common/Component';
import LinkButton from 'flarum/common/components/LinkButton';
export default class MobileTabItem extends Component {
  view(vnode) {
    const {
      route,
      icon,
      label
    } = this.attrs;
    return m(LinkButton, {
      href: route,
      icon: icon,
      title: label
    }, label);
  }
}
flarum.reg.add('acpl-mobile-tab', 'forum/components/MobileTabItem', MobileTabItem);